.add-application-content{
    background: white;
    width: 100%;
    margin-top: 40px;
    padding: 20px;
}
.create-device-btn{
    float: right;
    margin-top: -40px;
}
.device-name-area{
    width: 100%;
    height: 38px;
}
.device-name{
    width: 100%;
    height: 38px;
    border: 1px solid rgb(197, 196, 196);
    border-radius: 5px;
    display: inline-block;
    padding-left: 5px;
    cursor: pointer;
}
.device-add{
    width: 8%;
    height: 38px;
    border-radius: 0 5px 5px 0;
    cursor: pointer;
    background-color: rgb(106, 106, 238);
    padding: 3px;
    text-align: center;
    float: right;
}
.device-span{
    margin-top: 6px;
    margin-left: 2px;
    width: auto;
}
.device-cancel{
    background-color: rgb(228, 235, 240);
    height: 30px;
    cursor: pointer;
}
